import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';

export default function SimpleAccordion() {
  return (
    <div>
      <Accordion style= {{ backgroundColor:' #161515fc'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>Pourquoi cette application ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
           Cette application  pour but d'aider tout militaire de l'ecole de l'air souhaitant se prparer  l'examen du C.Q.O d'une faon interactive.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion style= {{ backgroundColor:' #161515fc'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography>Comment cela fonctionne ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Le questionnaire  est bas sur 38 questions, elles sont pioches alatoirement dans une large bande de donnes. La note final est convertie sur 20 et vous indiquera si vous avez suffisament 
            de connaissances pour la reussite du C.Q.O, la moyenne tant fix  12/20.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion style= {{ backgroundColor:' #161515fc'}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Typography>Pour qui est-t'elle destine ?</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
           Tout personnel militaire ou civil souhaitant dvelopper ses connaissances sur l'arme de l'air et de l'espace.
          </Typography>
        </AccordionDetails>
      </Accordion>
    </div>
  );
}
